import React,{Component} from 'react';
import classes from './Search1.css';
import {connect} from 'react-redux';
import * as actions from '../../store/actions/index';

class Search1 extends Component{
	state= {
		id: '',
    error: false
	};
changeHandler= (event)=>{
  event.preventDefault();
  const val= event.target.value;
  if( val.length === 0)
    this.setState({
      error: !this.state.error
    });
  this.setState({
    id: val
  });
}
 render(){
  console.log( this.props.error);
  let itm= this.props.searchedId.map(i =><li 
    key= {i.id}>
    NAME: {i.name}<br/>
     ID: {i.id}<br/>
     DESIGNATION: {i.designation}<br/>
      <a className={classes.link}
      target="_blank"
       rel="noopener noreferrer"
       href= {i.nasa_jpl_url}>LINK</a></li>);
    let cls= [classes.items];
    if(this.props.searchedId.length === 0)
      cls= [classes.Hide];
 	 return (
    <div className= {classes.searchBox}> 
       <div className= {classes.Input}>
       	 <input type= "number" placeholder= "enter asteroid id" 
       onChange= {this.changeHandler}/>
       <div>
         <button className= {classes.Button} 
          onClick= {()=>this.props.onFetchById(this.state.id)}> 
       SEARCH</button>
       </div>
       </div>
     {this.props.error ? <h1 className= {classes.error}> Enter a valid id</h1> :
     <div className= {cls}><span>{itm}</span></div>
     }
     </div>
    )
 }
}
const mapDispatchToProps= dispatch =>{
  return{
    onFetchById: (id)=> dispatch(actions.fetchById(id)),
    };
};
const mapStateToProps= state =>{
  return{
    asteroids: state.dates.items,
    searchedId: state.dates.itemsById,
    error: state.dates.error
  }
};
export default connect(mapStateToProps,mapDispatchToProps)(Search1);