import * as actions from './actionTypes';

export const fetchData= ()=>dispatch=>{
	fetch("https://api.nasa.gov/neo/rest/v1/neo/browse/?api_key=rUMAsp8IhAlems93UacOKIslTV02425e5CbE8oub").then(res => res.json())
	.then(data =>{
		return dispatch({
			type: actions.fetchAll, data: data
		});
	});
}
export const fetchByDate= (startDate, endDate) => dispatch =>{
	const start= new Date(startDate).getDate();
	const end= new Date(endDate).getDate();
	if(end===(start+7) || end<(start+7)){
		fetch(`https://cors-anywhere.herokuapp.com/https://api.nasa.gov/neo/rest/v1/feed?start_date=${startDate}&end_date=${endDate}&api_key=rUMAsp8IhAlems93UacOKIslTV02425e5CbE8oub`,
			 { headers: {'Access-Control-Request-Headers': 'Origin, X-Requested-With',
			 'Access-Control-Allow-Credentials': true
}})
		.then(res => res.json())
		.then(data =>{
			const d= {...data.near_earth_objects};
			return dispatch({
				type: actions.fetchByDates, data: d
			});
		});
		
	}
	else{
		return dispatch({ 
				type: actions.Error
			});
	}
}

export const fetchById= (id) =>dispatch =>{
	const i= parseInt(id);
	fetch(`https://cors-anywhere.herokuapp.com/https://
		api.nasa.gov/neo/rest/v1/neo/${i}?api_key=rUMAsp8IhAlems93UacOKIslTV02425e5CbE8oub`)
	.then((response) => {
		  if (response.ok) {
		    return response.json();
		  } else {
		     return dispatch({
		  	type: actions.Error
		  	});
		  }
		})
		.then(data =>{
					return dispatch({
						type: actions.fetchByIds, data: data,
						error: false
					});
				})
		.catch((error) => {
			 return dispatch({
		  	type: actions.Error,
		  	err: error
		  	});
		});
	}
