import React,{Component} from 'react';
import  classes from './App.css';
import {connect} from 'react-redux';
import Browse from './components/Browse/Browse';
import Search1 from './components/Search1/Search1';
import Search2 from './components/Search2/Search2';
import {Route, Switch,Redirect} from 'react-router-dom';
import * as actions from './store/actions/index';

class App extends Component{
  componentDidMount(){
    this.props.onFetch();
  }
  render(){
    // console.log(this.props.searchedId);
    //search by id
    let routes= (
      <Switch>
        <Route path= "/search1" exact  component= {Search1}/>
        <Route path= "/search2" exact  component= {Search2}/>
        <Route path= "/" component= {Browse}/>
        <Redirect to= '/'/>
      </Switch>
      )
     return (
        <div>
          <h1 className= {classes.heading}>
            NASA NeoWs
          </h1>
        <div className={classes.App}>{routes}</div>
       </div>
  );
  }
}
const mapDispatchToProps= dispatch =>{
  return{
    onFetch: ()=> dispatch(actions.fetchData()),
    onFetchById: (items,id)=> dispatch(actions.fetchById(items,id)),
    fetchDates: (startdate, endDate)=> dispatch(actions.fetchByDate(startdate, endDate))
  };
};
const mapStateToProps= state =>{
  return{
    asteroids: state.dates.items,
    searchedId: state.dates.itemsById
  }
};
export default connect(mapStateToProps,mapDispatchToProps)(App);
