import React from 'react';
import {connect} from 'react-redux';
import classes from './Browse.css';
import {Link} from 'react-router-dom';

class Browse extends React.Component {
	render() {
		return (
			<div className= {classes.List}>
				<table>
					<tbody>
						<tr>
							<th>REF_ID</th>
							<th>NAME</th>
							<th>URL</th>
						</tr>
					{this.props.asteroids.map(a =>
					 <tr key= {a.neo_reference_id}>
		        		<td>{a.id}</td>
		        		<td>{a.name}</td>      
		        		<td><a className= {classes.link}
		        		 href= {a.nasa_jpl_url}
		        		 target="_blank"
		        		 rel="noopener noreferrer">{a.nasa_jpl_url}</a></td>      
		        	</tr>)}
		        	</tbody>
				</table>
				<div className= {classes.wrapper}>
		          <div className= {classes.Search1}>
		            <h1><Link to= '/search1' className= {classes.link}>
		            	Search By ID</Link></h1>
		          </div>
		          <div className= {classes.Search2}>
		          	<h1><Link to= '/search2' className= {classes.link}>
		            	Search By Dates</Link></h1>
		        </div>
      </div>
      		</div>
		);
	}
}

const mapStateToProps= state =>{
	return{
		asteroids: state.dates.items
	};
};
export default connect(mapStateToProps)(Browse);