import React from 'react';
import classes from './Search2.css';
import {connect} from 'react-redux';
import * as actions from '../../store/actions/index';

class Search2 extends React.Component {
	state= {
    startDate: null,
    endDate: null,
    entries:[]
  	};

  	startDatePicker= event=>{
  		event.preventDefault();
  		var date= event.target.value;
	  this.setState({
	    startDate: date
	  });
	}
	 endDatePicker= (event)=>{
	  event.preventDefault();
	  let date= event.target.value;
	  this.setState({
	    endDate: date
	  });
	}
	submitHandler= (event) =>{
		event.preventDefault();
		this.props.onSearchByDate(this.state.startDate, this.state.endDate);
	}
	render() {
		console.log(this.props.search);
		let item=  Object.keys(this.props.search).map(key => {
						return <div key= {key}  className= {classes.Box}>
							<table>
								<tbody>
									<tr>
										<th><h2>{key}</h2></th>
									</tr>
									<tr>
										{this.props.search[key].map(i => 
											<td  className= {classes.td}key= {i.id}>
											NAME: {i.name} <br/>
											ID: {i.id}<br/>
											<a href= {i.nasa_jpl_url}
												className= {classes.link}>
												Link</a>
											</td>)
										}	
									</tr>
								</tbody>
							</table>
						</div>
					});
		console.log(this.props.error);
		return (
			<div>
				<div className= {classes.Input}>
					<input type= "date" placeholder= "yyyy-mm-dd"
						 onChange= {this.startDatePicker} name= "startDate"/><br/>
					<input type= "date" 
						onChange= {this.endDatePicker} name= "endDate"/>
					<button  className= {classes.Button}
						onClick= {this.submitHandler}>
						SEARCH</button>
				</div>
				<div>
					{this.props.error? <h1 className= {classes.error}>
					EndDate should be either equal or
					 upto 7 days of startdate</h1> : <div className= {classes.Wrapper}>
					 {item}
					 </div>}
				</div>
			</div>
		);
	}
}
const mapStateToProps= state =>{
	return{
		item: state.dates.item,
		search: state.dates.itemsByDate,
		error: state.dates.error
	};
};
const mapDispatchToProps= dispatch =>{
	return{
		onSearchByDate: (startDate, endDate)=> dispatch(actions.fetchByDate(startDate, endDate))
	}
}
export default connect(mapStateToProps, mapDispatchToProps)(Search2);