
const initialState={
	items:[],
	itemsByDate: [],
	itemsById: [],
	error: false
};

const datesReducer=(state=initialState,action) =>{
	switch(action.type){
		case 'FETCHALL':
			return{
				...state,
				items: [...action.data.near_earth_objects]
			}
		case 'FETCHBYDATES':
			return{
				...state,
				itemsByDate: action.data
			}
		case 'FETCHBYIDS':
				return{
					...state,
					itemsById: [action.data],
					error: action.error
				}
		case 'ERROR':
				return{
					...state,
					error: !state.error,
					itemsByDate:[]
				}		
		default:
			return{
				...state
			}
	}
};
export default datesReducer;